import React from "react";
import styled from "styled-components";
import Me from "../assets/images/me.png";
import { motion } from "framer-motion";

const Box = styled(motion.div)`
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  width: 65vw;
  height: 55vh;
  display: flex;

  background: linear-gradient(
        to right,
        ${(props) => props.theme.body} 50%,
        ${(props) => props.theme.text} 50%
      )
      bottom,
    linear-gradient(
        to right,
        ${(props) => props.theme.body} 50%,
        ${(props) => props.theme.text} 50%
      )
      top;

  background-repeat: no-repeat;
  background-size: 100% 1px;
  border-left: 2px solid ${(props) => props.theme.body};
  border-right: 1px solid ${(props) => props.theme.text};
  z-index: 1;

  @media (max-width: 800px) {
    width: 55vw;
    height: 65vh !important;
    display: block;
    background: linear-gradient(
          to right,
          ${(props) => props.theme.body} 0%,
          ${(props) => props.theme.text} 0%
        )
        bottom,
      linear-gradient(
          to right,
          ${(props) => props.theme.body} 100%,
          ${(props) => props.theme.text} 100%
        )
        top;
    background-repeat: no-repeat;
    background-size: 100% 1px;
  }
`;

const SubBox = styled.div`
  width: 50%;
  position: relative;
  display: flex;

  .pic {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 0%);
    width: 100%;
    height: auto;
  }

  @media (max-width: 800px) {
    border-left: 1px solid ${(props) => props.left};
    border-right: 1px solid ${(props) => props.right};
    right: 1px;
    width: 100%;
    height: 50%;
    .pic {
      width: calc(10rem + 1.5vw);
    }
  }
`;

const Text = styled.div`
font-size: calc(1em + 1.5vw);
color: ${(props) => props.theme.body};
padding: 1rem;
cursor: pointer;

display: flex;
flex-direction: column;
justify-content: space-evenly;

&>*:last-child{
    color: ${(props) => `rgba(${props.theme.bodyRgba},0.6)`}
    font-size: calc(0.5rem + 1.5vw);
    font-weight:300;
}
`;

const Intro = () => {
  return (
    <Box
      initial={{ height: 0 }}
      animate={{ height: "55vh" }}
      transition={{ type: "spring", duration: 2, delay: 1 }}
    >
      <SubBox right="#dad271">
        <Text>
          <h1>Hello,</h1>
          <h3>I'm Anthony Dube</h3>
          <h6>Full Stack Web Developer</h6>
        </Text>
      </SubBox>
      <SubBox left="#000000">
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1, delay: 2 }}
        >
          <img className="pic" src={Me} alt="Profile Pic" />
        </motion.div>
      </SubBox>
    </Box>
  );
};

export default Intro;
